 .navbar {
    display: flex;
    justify-content: center; 
    align-items: center; 
    margin-left: 48%;
  }
  
  .nav-list {
    list-style-type: none; 
    padding: 0; 
    margin: 0; 
    display: flex;
    margin-top: 3%;
  }
  
  .nav-item {
    margin: 0 60px; 
  }
  
  .nav-link {
    font-family: "Martian Mono", monospace;
    font-size: 1.5rem;
    color: white; 
    text-decoration: none; 
    font-weight: bold;
    display: inline-block;
    transition: color 0.5s ease, transform 0.3s ease;
  }
  
  .nav-link:hover {
    color: #98ff98;
    transform: scale(1.3); 
  }

  
@media screen and (max-width: 932px) {
  .navbar {
    display: flex; 
    justify-content: center; 
    align-items: center; 
    position: relative; /* Allows for positioning adjustments */
    top: -1vh;
    left: 1vh; 
    transform: translateX(-50%); 
    width: 100%; 
  }
  
  .nav-list {
    top: 5%;
    list-style-type: none; 
    padding: 0; 
    margin: 0; 
    display: flex;
    justify-content: center; 
    gap: 1rem; 
    flex-wrap: wrap; 
  }
  
  .nav-item {
    margin: 0 60px;
  }
  
  .nav-link {
    font-family: "Martian Mono", monospace;
    font-size: 1rem;
    color: white;
    text-decoration: none; 
    font-weight: bold; 
    display: inline-block;
    display: flex-box; 
    transition: color 0.5s ease, transform 0.3s ease; 
  }
}